import React, { useRef, useState, useEffect } from 'react'
import Bgcanvas from './Bgcanvas';
import canvas from './Bgcanvas'

export default function Canvas({d}) {

  var toMeridium = new Date();
  var hours = toMeridium.getHours() < 20 ? "day":"night";
  d(hours);

  const myCanvas = useRef();
  const myCheck = useRef();

  const [canvasObj, setCanvasObj] = useState();
  const [checkToggle, setCheckToggle] = useState(false);

  useEffect( () => {

      setCanvasObj( new canvas(myCanvas.current, checkToggle) );
      
  } ,[myCanvas])

  function toggle() {
    setCheckToggle(!checkToggle);
    canvasObj.check(checkToggle);
  }

  function star() {
    if(checkToggle == true){
      console.log(checkToggle);
        for(let i=0; i<100; i++){
            var ele = document.createElement('div');
    
            // document.querySelector('canvas').style.width = window.innerWidth;
            // document.querySelector('canvas').style.height = window.innerHeight;
    
            ele.classList.add('little-star');
            ele.style.top = `${Math.random() * 400}px`;
            ele.style.left = `${Math.random() * 10000}px`;
    
            // document.querySelector('body').style.width = `${document.body.clientWidth}px`;
            // document.querySelector('body').style.height = `${document.body.clientHeight}px`;
    
            document.querySelector('.star').appendChild(ele);
        }
    }
  }

  star()

return (
    <div>
      <input type="checkbox" id="c" onClick={toggle} ref={myCheck}/>
      <label for="c">
          <div id="a">
              <div id="c1"></div>
              <div id="c2"></div>
              <div id="c3"></div>
          </div>
      </label>

      <div className="star"></div>
      <canvas ref={myCanvas} onLoad={()=>{canvasObj.animate();}}>
      </canvas>
    </div>
  );
}